/* Navbar Styling */
*{
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
}

.navbar {
    display: flex;
    justify-content: center;
    background-color: #007bff;
    padding: 1rem 0; /* Adjust padding as needed */
    margin: 0%; /* Ensure no unwanted gaps */
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 1000;
  }
  
  .nav-links {
    list-style: none;
    display: flex;
    gap: 2rem;
    margin: 0;
    padding: 0;
    z-index: 2;
  }
  
  .nav-links li {
    font-size: 1rem;
  }
  
  .nav-links a {
    text-decoration: none;
    color: #fff;
    transition: color 0.3s;
    
  }
  
  .nav-links a:hover {
    color: #d1e0ff;
  }
  
  .nav-links .active {
    font-weight: bold;
    color: #000;
    background-color: #fff;
    padding: 5px 10px;
    border-radius: 5px;
  }
  
  /* Page Content Styling */
  .creator-container {
    text-align: center;
  }
  
  .content{
    margin-top: 4rem;
  }
  
  .content h1 {
    font-size: 2rem;
    margin-top: 1rem;
  }
  
  .content p {
    font-size: 1.2rem;
    color: #555;
  }
  
  .google-login-btn{
    padding: 5px 10px;
    margin: 0%;
    border-radius: 0.3rem;
    border: 2px double rgb(0, 0, 0);
    background-color: #ffffff;
    color: #070707;
    cursor: pointer;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.541);
    transition: 0.5s;
    display: flex;
    justify-content: center;
    font-weight: bold;
    z-index: 999;
  }

  .google-login-btn:hover{
    box-shadow: inset 0px 0px 10px rgba(255, 255, 255, 0.432);
    background-color: #000000;
    color: #ffffff;
    border: 2px double rgb(255, 255, 255);
  }

  .google-login-container{
    width: 100%;
    display: flex;
    position: fixed;
    justify-content: end;
    margin: -0.3rem 0rem 0rem -1rem;
    z-index: 1;
  }

  .google-login-btn img{
    width: 1rem;
    margin-right: 0.3rem;
  }

  .sign-out-btn i{
    width: 1rem;
    margin-right: 0.3rem;
    display: flex;
    margin-top: 2px;
  }

  .sign-out-btn{
    padding: 5px 10px;
    margin: 0%;
    border-radius: 0.3rem;
    border: 2px solid rgb(253, 253, 253);
    background-color: #ff0000;
    color: #ffffff;
    cursor: pointer;
    transition: 0.5s;
    display: flex;
    justify-content: center;
    font-weight: bold;
  }

  .sign-out-btn:hover{
    box-shadow: inset 0px 0px 10px rgba(255, 255, 255, 0.432);
    background-color: #000000;
    color: #ffffff;
    border: 2px double rgb(255, 255, 255);
  }

  /* Bottom Navbar Styling */
.bottom-navbar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #007bff;
    padding: 0.8rem 0;
    position: fixed;
    bottom: 0;
    width: 100%;
    z-index: 1000;
    box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .bottom-navbar a {
    text-decoration: none;
    color: #fff;
    font-size: 15px;
    transition: color 0.3s;
  }
  
  .bottom-navbar a.active {
    color: #000;
    font-weight: bold;
    background-color: #fff;
    padding: 5px 10px;
    border-radius: 5px;
  }
  
  .bottom-navbar a:hover {
    color: #d1e0ff;
  }
  
  .bottom-navbar i {
    font-size: 1rem;
    margin-right: 0.5rem;
  }
  
  /* Add some padding to the content to avoid overlap with the bottom navbar */
  .content {
    padding-bottom: 4rem;
  }

  .navbar .logo{
    width: 4%;
    padding: 0%;
    margin: 0%;
    display: none;
    z-index: 2;
}

    .bottom-navbar{
        display: none;
    }
  
  @media (max-width: 547px) {
    .navbar li{
        display: none;
    }

    .navbar .logo{
        width: 13%;
        padding: 0%;
        margin: 0%;
        display: flex;
    }
    
    .navbar{
        padding: 5px 0%;
    }

    .google-login-btn{
        margin-top: 2.2%;
        z-index: 1;
    }

    .sign-out-btn{
      margin-top: 2.2%;
        z-index: 1;
    }

    .bottom-navbar{
        display: flex;
    }
    
  }